.ToolItem{
    font-family: Bebas Neue;
    font-size: 18px;
    color: black;
}
.ExploreItem::after{
    content: "";
    display: block;
    position: absolute;
    margin-top: 30px;
    width: 80px;
    border: 3px solid #02e3dc;
    border-radius: 20px 20px 0px 0px;
    transform: scale(0);
    transition: all 0.5s;
}
.ExploreItem:hover{
    color: #02e3dc;
}
.SellItem:hover{
    color: #02e3dc;
}
.SellItem:hover::after{
    transform: scale(1);
}
.ExploreItem:hover::after{
    transform: scale(1);
}
.SellItem::after{
    content: "";
    display: block;
    border: 3px solid #02e3dc;
    position: absolute;
    margin-top: 30px;
    border-radius: 20px 20px 0px 0px;
    width: 40px;
    transform: scale(0);
    transition: all 0.5s;
}
.HeaderTool{
